"use client";

import { useState, useEffect } from "react";
import { Users, Trash2, Search, User, Store, AlertTriangle, CheckCircle } from "lucide-react";
import { Montserrat } from "next/font/google";

const montserrat = Montserrat({
  subsets: ["latin"],
  weight: ["800", "400"],
});

interface Client {
  id: number;
  firstName: string;
  lastName: string;
  email: string;
  createdAt: string;
  phone?: string;
  address?: {
    street: string;
    province: string;
    postalCode: string;
  };
  image?: string;
}

interface Seller {
  id: number;
  firstName: string;
  lastName: string;
  email: string;
  createdAt: string;
  shopName: string;
  phone?: string;
  address?: {
    street: string;
    province: string;
    postalCode: string;
  };
  reports?: number;
  image?: string;
}

export default function UsersPage() {
  const [clients, setClients] = useState<Client[]>([
    {
      id: 1,
      firstName: "Ahmed",
      lastName: "Ben Salah",
      email: "ahmed.bensalah@example.com",
      createdAt: "2024-01-15",
      phone: "+216 20 123 456",
      address: { street: "Rue Tunis", province: "Tunis", postalCode: "1000" },
      image: "https://randomuser.me/api/portraits/men/1.jpg",
    },
    {
      id: 2,
      firstName: "Fatima",
      lastName: "Zohra",
      email: "fatima.zohra@example.com",
      createdAt: "2024-03-22",
      phone: "+216 21 456 789",
      address: { street: "Avenue Habib", province: "Sousse", postalCode: "4000" },
      image: "https://randomuser.me/api/portraits/women/2.jpg",
    },
    {
      id: 3,
      firstName: "Youssef",
      lastName: "Khalil",
      email: "youssef.khalil@example.com",
      createdAt: "2024-05-10",
      phone: "+216 23 789 123",
      address: { street: "Rue Kairouan", province: "Kairouan", postalCode: "3100" },
      image: "https://randomuser.me/api/portraits/men/3.jpg",
    },
  ]);

  const [sellers, setSellers] = useState<Seller[]>([
    {
      id: 1,
      firstName: "Karim",
      lastName: "Haddad",
      email: "karim.haddad@example.com",
      createdAt: "2023-11-05",
      shopName: "Karim Fashion",
      phone: "+216 22 987 654",
      address: { street: "Avenue Carthage", province: "Sfax", postalCode: "3000" },
      reports: 2,
      image: "https://randomuser.me/api/portraits/men/2.jpg",
    },
    {
      id: 2,
      firstName: "Leila",
      lastName: "Mansour",
      email: "leila.mansour@example.com",
      createdAt: "2024-02-18",
      shopName: "Leila Boutique",
      phone: "+216 24 654 321",
      address: { street: "Rue Jendouba", province: "Jendouba", postalCode: "8100" },
      reports: 0,
      image: "https://randomuser.me/api/portraits/women/1.jpg",
    },
  ]);

  const [clientSearch, setClientSearch] = useState("");
  const [sellerSearch, setSellerSearch] = useState("");
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [userToDelete, setUserToDelete] = useState<{
    id: number;
    type: "client" | "seller";
  } | null>(null);
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [selectedUser, setSelectedUser] = useState<Client | Seller | null>(null);

  const filteredClients = clients.filter(
    (client) =>
      client.firstName.toLowerCase().includes(clientSearch.toLowerCase()) ||
      client.lastName.toLowerCase().includes(clientSearch.toLowerCase())
  );

  const filteredSellers = sellers.filter(
    (seller) =>
      seller.firstName.toLowerCase().includes(sellerSearch.toLowerCase()) ||
      seller.lastName.toLowerCase().includes(sellerSearch.toLowerCase()) ||
      seller.shopName.toLowerCase().includes(sellerSearch.toLowerCase())
  );

  const handleDeleteUser = (id: number, type: "client" | "seller") => {
    setUserToDelete({ id, type });
    setShowDeleteModal(true);
  };

  const confirmDelete = () => {
    if (userToDelete) {
      if (userToDelete.type === "client") {
        setClients(clients.filter((client) => client.id !== userToDelete.id));
        setSuccessMessage("Client supprimé avec succès !");
      } else {
        setSellers(sellers.filter((seller) => seller.id !== userToDelete.id));
        setSuccessMessage("Vendeur supprimé avec succès !");
      }
      setShowDeleteModal(false);
      setUserToDelete(null);
    }
  };

  const cancelDelete = () => {
    setShowDeleteModal(false);
    setUserToDelete(null);
  };

  const handleUserClick = (user: Client | Seller) => {
    setSelectedUser(user);
  };

  const closeUserDetails = () => {
    setSelectedUser(null);
  };

  const handleWarnSeller = (id: number, sellerName: string) => {
    setSuccessMessage(`Avertissement envoyé à ${sellerName} avec succès !`);
  };

  useEffect(() => {
    if (successMessage) {
      const timer = setTimeout(() => {
        setSuccessMessage(null);
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [successMessage]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-[#F0F8FF] via-[#E6F0FA] to-[#B0E0E6]/30 py-12 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
      {/* Effet de particules scintillantes */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="absolute top-10 left-20 w-2 h-2 bg-[#FFD700] rounded-full animate-sparkle" style={{ animationDelay: "0s" }} />
        <div className="absolute top-40 right-30 w-3 h-3 bg-[#87CEEB] rounded-full animate-sparkle" style={{ animationDelay: "1s" }} />
        <div className="absolute bottom-20 left-40 w-2 h-2 bg-[#FFD700] rounded-full animate-sparkle" style={{ animationDelay: "2s" }} />
        <div className="absolute bottom-40 right-10 w-3 h-3 bg-[#87CEEB] rounded-full animate-sparkle" style={{ animationDelay: "3s" }} />
      </div>

      <div className="max-w-7xl mx-auto relative z-10">
        {/* Message de succès */}
        {successMessage && (
          <div className="fixed top-4 right-4 z-50">
            <div className="bg-gradient-to-r from-[#87CEEB] to-[#FFD700] text-white px-6 py-3 rounded-xl shadow-2xl flex items-center gap-3 transform transition-all duration-500 ease-in-out animate-bounce-in border border-[#FFD700]/70">
              <CheckCircle className="h-6 w-6" />
              <span className={`font-semibold ${montserrat.className}`}>{successMessage}</span>
              <button
                onClick={() => setSuccessMessage(null)}
                className="text-white hover:text-[#F0F8FF] transition-colors duration-200"
              >
                ✕
              </button>
            </div>
          </div>
        )}

        {/* En-tête */}
        <div className="mb-10 flex flex-col lg:flex-row lg:items-center lg:justify-between gap-6">
          <div className="flex items-center gap-4">
            <Users className="h-12 w-12 text-[#FFD700] animate-glow" />
            <h1
              className={`text-6xl font-extrabold text-[#1F2937] tracking-tight ${montserrat.className} bg-gradient-to-r from-[#FFD700] to-[#87CEEB] bg-clip-text text-transparent animate-glow-text`}
            >
              Gestion des Utilisateurs
            </h1>
          </div>
          {/* Cartes Total Clients et Total Vendeurs (inchangées) */}
          <div className="flex flex-col sm:flex-row gap-4">
            <div className="group bg-white/30 backdrop-blur-lg border border-gray-200/50 p-6 rounded-xl shadow-xl transform transition-all duration-300 hover:-translate-y-2 hover:shadow-2xl">
              <div className="flex items-center gap-4">
                <User className="h-10 w-10 text-blue-500 group-hover:scale-110 transition-transform duration-300" />
                <div>
                  <h3 className="text-lg font-semibold text-gray-800">Total Clients</h3>
                  <p className="text-2xl font-bold text-gray-900 mt-1">{clients.length}</p>
                </div>
              </div>
            </div>
            <div className="group bg-white/30 backdrop-blur-lg border border-gray-200/50 p-6 rounded-xl shadow-xl transform transition-all duration-300 hover:-translate-y-2 hover:shadow-2xl">
              <div className="flex items-center gap-4">
                <Store className="h-10 w-10 text-purple-500 group-hover:scale-110 transition-transform duration-300" />
                <div>
                  <h3 className="text-lg font-semibold text-gray-800">Total Vendeurs</h3>
                  <p className="text-2xl font-bold text-gray-900 mt-1">{sellers.length}</p>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Section Clients */}
        <div className="mb-12">
          <h2 className={`text-4xl font-bold text-[#1F2937] mb-6 ${montserrat.className} bg-gradient-to-r from-[#FFD700] to-[#87CEEB] bg-clip-text text-transparent animate-glow-text`}>
            Clients
          </h2>
          <div className="mb-6 flex items-center gap-3">
            <div className="relative w-full max-w-md">
              <input
                type="text"
                value={clientSearch}
                onChange={(e) => setClientSearch(e.target.value)}
                placeholder="Rechercher par nom ou prénom..."
                className="w-full px-4 py-3 bg-white/50 border border-[#87CEEB]/70 rounded-xl focus:ring-2 focus:ring-[#FFD700] focus:border-[#FFD700] focus:outline-none transition-all duration-300 text-gray-800 placeholder-gray-400 shadow-lg hover:bg-white/70 animate-glow-input"
              />
              <Search className="absolute right-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-[#FFD700]" />
            </div>
          </div>
          <div className="bg-white/20 backdrop-blur-2xl border border-[#87CEEB]/50 rounded-xl shadow-2xl overflow-x-auto animate-fade-in">
            <table className="min-w-full divide-y divide-[#FFD700]/30">
              <thead className="bg-gradient-to-r from-[#F0F8FF]/80 to-[#B0E0E6]/80">
                <tr>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Nom</th>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Prénom</th>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Email</th>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Date de création</th>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Actions</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-[#FFD700]/30">
                {filteredClients.length > 0 ? (
                  filteredClients.map((client) => (
                    <tr
                      key={client.id}
                      className="hover:bg-[#B0E0E6]/20 transition-all duration-300 cursor-pointer"
                      onClick={() => handleUserClick(client)}
                    >
                      <td className="px-6 py-4 text-sm text-gray-800">{client.lastName}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{client.firstName}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{client.email}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{client.createdAt}</td>
                      <td className="px-6 py-4">
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            handleDeleteUser(client.id, "client");
                          }}
                          className="bg-gradient-to-r from-[#FF4500] to-[#FF69B4] text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:from-[#FF69B4] hover:to-[#FF4500] transition-all duration-300 shadow-lg hover:shadow-[#FFD700]/50 animate-glow-button"
                        >
                          <Trash2 className="h-4 w-4" /> Supprimer
                        </button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={5} className="px-6 py-4 text-center text-sm text-gray-600">
                      Aucun client trouvé.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>

        {/* Section Vendeurs */}
        <div>
          <h2 className={`text-4xl font-bold text-[#1F2937] mb-6 ${montserrat.className} bg-gradient-to-r from-[#FFD700] to-[#87CEEB] bg-clip-text text-transparent animate-glow-text`}>
            Vendeurs
          </h2>
          <div className="mb-6 flex items-center gap-3">
            <div className="relative w-full max-w-md">
              <input
                type="text"
                value={sellerSearch}
                onChange={(e) => setSellerSearch(e.target.value)}
                placeholder="Rechercher par nom, prénom ou nom de boutique..."
                className="w-full px-4 py-3 bg-white/50 border border-[#87CEEB]/70 rounded-xl focus:ring-2 focus:ring-[#FFD700] focus:border-[#FFD700] focus:outline-none transition-all duration-300 text-gray-800 placeholder-gray-400 shadow-lg hover:bg-white/70 animate-glow-input"
              />
              <Search className="absolute right-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-[#FFD700]" />
            </div>
          </div>
          <div className="bg-white/20 backdrop-blur-2xl border border-[#87CEEB]/50 rounded-xl shadow-2xl overflow-x-auto animate-fade-in">
            <table className="min-w-full divide-y divide-[#FFD700]/30">
              <thead className="bg-gradient-to-r from-[#F0F8FF]/80 to-[#B0E0E6]/80">
                <tr>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Nom</th>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Prénom</th>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Email</th>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Date de création</th>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Nom de boutique</th>
                  <th className={`px-6 py-3 text-left text-sm font-bold text-[#1F2937] ${montserrat.className}`}>Actions</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-[#FFD700]/30">
                {filteredSellers.length > 0 ? (
                  filteredSellers.map((seller) => (
                    <tr
                      key={seller.id}
                      className="hover:bg-[#B0E0E6]/20 transition-all duration-300 cursor-pointer"
                      onClick={() => handleUserClick(seller)}
                    >
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.lastName}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.firstName}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.email}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.createdAt}</td>
                      <td className="px-6 py-4 text-sm text-gray-800">{seller.shopName}</td>
                      <td className="px-6 py-4 flex gap-2">
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            handleWarnSeller(seller.id, `${seller.firstName} ${seller.lastName}`);
                          }}
                          className="bg-gradient-to-r from-[#FFA500] to-[#FFD700] text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:from-[#FFD700] hover:to-[#FFA500] transition-all duration-300 shadow-lg hover:shadow-[#87CEEB]/50 animate-glow-button"
                        >
                          <AlertTriangle className="h-4 w-4" /> Avertir
                        </button>
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            handleDeleteUser(seller.id, "seller");
                          }}
                          className="bg-gradient-to-r from-[#FF4500] to-[#FF69B4] text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:from-[#FF69B4] hover:to-[#FF4500] transition-all duration-300 shadow-lg hover:shadow-[#FFD700]/50 animate-glow-button"
                        >
                          <Trash2 className="h-4 w-4" /> Supprimer
                        </button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={6} className="px-6 py-4 text-center text-sm text-gray-600">
                      Aucun vendeur trouvé.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>

        {/* Modale de confirmation pour la suppression */}
        {showDeleteModal && (
          <div className="fixed inset-0 bg-black/50 backdrop-blur-md flex items-center justify-center z-50 animate-fade-in">
            <div className="bg-white/30 backdrop-blur-2xl border border-[#FFD700]/70 p-6 rounded-xl shadow-2xl max-w-sm w-full transform transition-all duration-300 hover:shadow-[#87CEEB]/50 animate-glow-modal">
              <h3 className={`text-xl font-semibold text-[#1F2937] mb-4 ${montserrat.className}`}>
                Confirmer la suppression
              </h3>
              <p className="text-gray-700 mb-6">
                Êtes-vous sûr de vouloir supprimer cet utilisateur ?
              </p>
              <div className="flex justify-end gap-3">
                <button
                  onClick={cancelDelete}
                  className="bg-gradient-to-r from-[#F0F8FF] to-[#B0E0E6] text-[#1F2937] px-4 py-2 rounded-lg font-semibold hover:from-[#B0E0E6] hover:to-[#F0F8FF] transition-all duration-300 shadow-md"
                >
                  Annuler
                </button>
                <button
                  onClick={confirmDelete}
                  className="bg-gradient-to-r from-[#FF4500] to-[#FF69B4] text-white px-4 py-2 rounded-lg font-semibold hover:from-[#FF69B4] hover:to-[#FF4500] transition-all duration-300 shadow-md"
                >
                  Confirmer
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Carte de détails de l'utilisateur (inchangée) */}
        {selectedUser && (
          <div className="fixed inset-0 bg-black/70 backdrop-blur-md flex items-center justify-center z-50 animate-fade-in">
            <div className="bg-white rounded-2xl shadow-2xl max-w-lg w-full p-6 transform transition-all duration-300 scale-100 hover:scale-105">
              <div className="relative">
                <button
                  onClick={closeUserDetails}
                  className="absolute top-2 right-2 text-gray-500 hover:text-gray-800 transition-colors duration-200"
                >
                  ✕
                </button>
                <div className="flex flex-col items-center">
                  <img
                    src={selectedUser.image || "https://via.placeholder.com/150"}
                    alt={`${selectedUser.firstName} ${selectedUser.lastName}`}
                    className="w-32 h-32 rounded-full object-cover border-4 border-gray-200 shadow-lg mb-4"
                  />
                  <h3 className="text-2xl font-bold text-gray-900">
                    {selectedUser.firstName} {selectedUser.lastName}
                  </h3>
                  <p className="text-gray-600 mb-4">{selectedUser.email}</p>

                  <div className="w-full space-y-3">
                    <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                      <span className="text-gray-700 font-semibold">Date de création :</span>
                      <span className="text-gray-900">{selectedUser.createdAt}</span>
                    </div>
                    <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                      <span className="text-gray-700 font-semibold">Téléphone :</span>
                      <span className="text-gray-900">{selectedUser.phone || "Non renseigné"}</span>
                    </div>
                    <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                      <span className="text-gray-700 font-semibold">Adresse :</span>
                      <span className="text-gray-900">
                        {selectedUser.address
                          ? `${selectedUser.address.street}, ${selectedUser.address.province}, ${selectedUser.address.postalCode}`
                          : "Non renseignée"}
                      </span>
                    </div>
                    {"shopName" in selectedUser && (
                      <>
                        <div className="flex justify-between items-center bg-purple-50 p-3 rounded-lg">
                          <span className="text-purple-700 font-semibold">Nom de la boutique :</span>
                          <span className="text-purple-900 font-bold">{selectedUser.shopName}</span>
                        </div>
                        <div className="flex justify-between items-center bg-red-50 p-3 rounded-lg">
                          <span className="text-red-700 font-semibold">Signalements :</span>
                          <span className="text-red-900 font-bold">{selectedUser.reports || 0}</span>
                        </div>
                      </>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Styles CSS personnalisés */}
      <style jsx>{`
        @keyframes fade-in {
          from { opacity: 0; }
          to { opacity: 1; }
        }
        @keyframes bounce-in {
          0% { transform: scale(0.9); opacity: 0; }
          60% { transform: scale(1.05); opacity: 1; }
          100% { transform: scale(1); }
        }
        @keyframes sparkle {
          0% { transform: scale(0.5); opacity: 0; }
          50% { transform: scale(1.2); opacity: 0.8; }
          100% { transform: scale(0.5); opacity: 0; }
        }
        @keyframes glow {
          0%, 100% { filter: drop-shadow(0 0 5px rgba(255, 215, 0, 0.5)); }
          50% { filter: drop-shadow(0 0 10px rgba(255, 215, 0, 0.8)); }
        }
        @keyframes glow-text {
          0%, 100% { text-shadow: 0 0 5px rgba(255, 215, 0, 0.5); }
          50% { text-shadow: 0 0 10px rgba(135, 206, 235, 0.8); }
        }
        @keyframes glow-input {
          0%, 100% { box-shadow: 0 0 5px rgba(135, 206, 235, 0.5); }
          50% { box-shadow: 0 0 15px rgba(255, 215, 0, 0.8); }
        }
        @keyframes glow-button {
          0%, 100% { box-shadow: 0 0 5px rgba(255, 215, 0, 0.5); }
          50% { box-shadow: 0 0 15px rgba(135, 206, 235, 0.8); }
        }
        @keyframes glow-modal {
          0%, 100% { box-shadow: 0 0 10px rgba(255, 215, 0, 0.5); }
          50% { box-shadow: 0 0 20px rgba(135, 206, 235, 0.8); }
        }
        .animate-fade-in {
          animation: fade-in 0.5s ease-in-out;
        }
        .animate-bounce-in {
          animation: bounce-in 0.5s ease-in-out;
        }
        .animate-sparkle {
          animation: sparkle 3s infinite ease-in-out;
        }
        .animate-glow {
          animation: glow 2s infinite ease-in-out;
        }
        .animate-glow-text {
          animation: glow-text 3s infinite ease-in-out;
        }
        .animate-glow-input {
          animation: glow-input 2s infinite ease-in-out;
        }
        .animate-glow-button {
          animation: glow-button 2s infinite ease-in-out;
        }
        .animate-glow-modal {
          animation: glow-modal 2s infinite ease-in-out;
        }
      `}</style>
    </div>
  );
}